import React, { useContext, useRef, useEffect } from "react";
import { scaleOrdinal, schemeCategory20 } from "d3-scale";
import { arc, pie } from "d3-shape";
import { Text } from 'office-ui-fabric-react/lib/Text';
import { List } from 'office-ui-fabric-react/lib/List';
import moment from "moment";
import { IncidentsContext } from "../../context";

export default () => {
    const {incidents, monthly} = useContext(IncidentsContext);
    const pieChart = useRef(null);

    const margin = {top:20,right:20,bottom:20,left:20};
    const width = 960, height = 500, radius = Math.min(width,height)/2;
    // const radius = 32;

    const color = scaleOrdinal(schemeCategory20);

    const dataarc = arc().outerRadius(radius-10).innerRadius(0);
    const labelarc = arc().outerRadius(radius-40).innerRadius(radius-40);

    const piechart = pie().sort(null).value(d => d.population);

    const lineOptions = {
        chart: {type: 'areaspline'},
        title: {text: 'Impact Trend'},
        legend: {enabled: false},
        xAxis: {
            categories: monthly.map(item => item.name)
        },
        yAxis: {
            title: {
                text: null
            },
            tickAmount: 3,
            labels: {
                formatter: function () {
                    return `${moment.duration(this.value, "milliseconds").humanize()}`;
                }
            }
        },
        tooltip: {
            formatter: function() {
                return `<b>${this.x}</b> Impact: <b>${moment.duration(this.y, "milliseconds").humanize()}</b>`;
            }
        },
        series: [{
            data: monthly.map(item => item.out)
        }]
    };

    useEffect(() => {
        try {
            
        } catch (er) {
            console.log("d3select",er);
        }
        
    },[]);

    const data = [
        {label:"<5", value:2704659},
        {label:"5-13", value:4489350},
        {label:"14-17", value:2159901},
        {label:"18-24", value:3853788},
        {label:"25-44", value:14106543},
        {label:"45-64", value:8819342},
        {label:">64", value:612463},
    ];

    const textStyle = {
        font: "10px sans-serif",
        textAnchor: "middle"
    }, pathStyle = { stroke: "#fff" };

    return <>
        <Text variant={"mega"} styles={{root:{marginLeft:48,marginTop:48}}} block>SUNRXS</Text>
        <Text variant={"large"} styles={{root:{marginLeft:54,marginRight:48}}} block>Report Management Tool</Text>
        <hr/>

        <div className="animated zoomIn faster">
            <svg width={width} height={height}>
                <g transform={`translate(${radius},${radius})`}>
                    {piechart(data).map((d,i) => (<g key={i}>
                        <path d={dataarc(d)} fill={color(d.data.label)}/>
                        <text dy=".35em" transform={`translate(${labelarc.centroid(d)})`}>{d.data.label}</text>
                    </g>))}
                </g>
            </svg>
        </div>    
    </>
}