import React, { useState } from "react";
import { DefaultButton } from "office-ui-fabric-react/lib/Button";
import { Modal } from 'office-ui-fabric-react/lib/Modal';
import { Text } from 'office-ui-fabric-react/lib/Text';

export default (props) => {
    const [show, setshow] = useState(false);
    const {startText,impact,endText,actions} = props.data;

    return <React.Fragment>
        <DefaultButton styles={{root:{marginTop:12,marginRight:4}}} text="More" onClick={() => setshow(true)}/>
        <Modal isOpen={show} isBlocking={true}>
            <div style={{display:"grid",gridTemplateRows:"repeat(5,auto)",justifyItems:"start",padding:24}}>
                <Text variant={"xxLarge"} styles={{root:{margin:12}}} block>{impact}</Text>
                <Text variant={"small"} styles={{root:{marginLeft:12}}} block>{startText} to {endText}</Text>
                <Text variant={"mediumPlus"} styles={{root:{margin:12}}} block>{actions}</Text>
                <DefaultButton styles={{root:{margin:4,justifySelf:"end"}}} onClick={() => setshow(false)} text="Close"/>
            </div>
        </Modal>
    </React.Fragment>;
}