import React, { useState } from "react";
import { PrimaryButton, DefaultButton } from "office-ui-fabric-react/lib/Button";
import { Modal } from 'office-ui-fabric-react/lib/Modal';
import { TextField } from 'office-ui-fabric-react/lib/TextField';
import { Dropdown } from 'office-ui-fabric-react/lib/Dropdown';
import firebase from "../../../fire";

export default (props) => {
    const [show, setshow] = useState(false);
    const [application, setapplication] = useState(props.data.application);
    const [description, setdescription] = useState(props.data.description);
    const [priority, setpriority] = useState(props.data.priority);
    const [resolver, setresolver] = useState(props.data.resolver);

    const handleSubmit = (event) => {
        event.preventDefault();

        const body = props.data;
        body.application = application;
        body.description = description;
        body.priority = priority;
        body.resolver = resolver;

        firebase.database().ref().child("incidents").child(body.id).set(body);
        setShow(false);
    }

    const getErrorMessage = (value) => {
        return value.length > 1 ? '' : `Input value length must be more than 3.`;
    }

    return <>
        <PrimaryButton styles={{root:{marginTop:12,marginRight:4}}} text="Edit" onClick={() => setshow(true)}/>
        <Modal isOpen={show} isBlocking={true}>
            <div style={{display:"grid",gridTemplateRows:"repeat(5,auto)",justifyItems:"start",padding:24}}>
                
                <TextField label="Which application|s have been impacted" defaultValue={application} onChange={(e) => setapplication(e.target.value)} onGetErrorMessage={getErrorMessage}/>
                <TextField label="Fully detail the cause of the issue" defaultValue={description} multiline rows={5} onChange={(e) => setdescription(e.target.value)} onGetErrorMessage={getErrorMessage}/>
                <Dropdown label="Priority" selectedKey={priority} onChange={e => setpriority(e.target.value)} placeholder="Select an option" options={[
                    {key:'1-Extensive/Widespread',text:'1-Extensive/Widespread'},
                    {key:'2-Significant/Large',text:'2-Significant/Large'},
                    {key:'3-Moderate/Limited',text:'3-Moderate/Limited'}
                ]} styles={{dropdown:{width:300}}}/>
                <TextField label="Who is working on the issue" defaultValue={resolver} onChange={(e) => setresolver(e.target.value)} onGetErrorMessage={getErrorMessage}/>

                <div style={{padding:4,justifySelf:"end"}}>
                    <PrimaryButton styles={{root:{margin:4}}} text="Submit" onClick={handleSubmit}/>
                    <DefaultButton styles={{root:{margin:4}}} onClick={() => setshow(false)} text="Cancel"/>
                </div>
            </div>
        </Modal>
    </>;
}