import React, { useContext, useState } from "react";
import { Text } from 'office-ui-fabric-react/lib/Text';
import { SearchBox } from 'office-ui-fabric-react/lib/SearchBox';
import IncidentCard from "./incident/_card";
import { IncidentsContext } from "../../context";

export default () => {
    const {incidents} = useContext(IncidentsContext);
    const [searches, setSearches] = useState("Search application");
    
    const getFirebase = () => {
        setSearches("Search application");
    };

    const searchEngine = () => {
        if (searches !== "") {};
    };
    
    return <>
        <Text variant={"mega"} styles={{ root: { marginLeft: 48, marginTop: 48 } }} block>Incidents</Text>
        <Text variant={"large"} styles={{ root: { marginLeft: 54, marginRight: 48 } }} block>These are categorised by date and time, ensuring recent events are prioritised.</Text>
        <SearchBox styles={{ root: { margin: 38 } }} placeholder={searches} underlined={true} onChange={e => setSearches(e.target.value)} onSearch={searchEngine} onClear={getFirebase}/>

        <div style={{display: "flex", flexWrap: "wrap", justifyContent: "space-around"}}>{incidents ? incidents.map(item => <div key={item.id} className="animated zoomIn faster"><IncidentCard data={item}/></div>) : <div className="spinner-border" role="status">
            <span className="sr-only">Loading...</span>
        </div>}</div>
    </>
};