import React from "react";
import { Icon } from 'office-ui-fabric-react/lib/Icon';
import { Text } from 'office-ui-fabric-react/lib/Text';
import MoreInfo from "./_more_info";
import Update from "./_update";
import Edit from "./_edit";
import firebase from "../../../fire";


export default (props) => {
    const {application,description,impact,startText,end,outageText,priority} = props.data;

    const textWithIconStyle = {
        display:"grid",
        gridGap:"4px",
        gridTemplateColumns:"1rem auto", 
        justifyItems:"start",
        alignItems:"center"
    }

    const resolve = () => {
        const body = props.data
        body.end = `${new Date()}`;
        firebase.database().ref().child("incidents").child(body.id).set(body);
    }

    return <div style={{padding:24,width:"24rem"}}>
        <Text variant={"xxLarge"} block>{application}</Text>
        <Text variant={"small"} block>{startText}</Text>
        <Text variant={"mediumPlus"} styles={{root:{margin:12}}} block>{impact}</Text>
        <div style={textWithIconStyle}><Icon iconName="Warning"/><Text variant={"small"} block>{priority}</Text></div>
        <div style={textWithIconStyle}><Icon iconName="BufferTimeAfter"/><Text variant={"small"} block>{outageText}</Text></div>
        <div style={textWithIconStyle}><Icon styles={{root:{alignSelf:"start"}}} iconName="TextDocument"/><Text variant={"medium"} block>{description}</Text></div>

        <div style={{display:"grid",gridGap:"4px",gridTemplateColumns:"repeat(3,5rem)"}}>
            <MoreInfo data={props.data}/>
            <Update data={props.data}/>
            <Edit data={props.data}/>
            {!end ? <PrimaryButton text="Resolve" onClick={resolve}/> : null}
        </div>
    </div>;
}