import React, { useRef, useState } from "react";
import { PrimaryButton, DefaultButton } from "office-ui-fabric-react/lib/Button";
import { Modal } from 'office-ui-fabric-react/lib/Modal';
import { TextField } from 'office-ui-fabric-react/lib/TextField';
import firebase from "../../../fire";

export default (props) => {
    const [show, setshow] = useState(false);
    const [editedText, seteditedText] = useState("");
    const myRef = useRef(null);

    const handleSubmit = (event) => {
        event.preventDefault();

        if (editedText !== "") {
            const body = props.data;
            body.actions = `${body.actions}. ${editedText}`;

            firebase.database().ref().child("incidents").child(body.id).set(body);
            
            myRef.current.value = "";
            console.log(body);
            setshow(false);
        }
    }

    const getErrorMessage = (value) => {
        return value.length > 3 ? '' : `Input value length must be more than 3.`;
    }

    return <>
        <PrimaryButton styles={{root:{marginTop:12,marginRight:4}}} text="Update" onClick={() => setshow(true)}/>
        <Modal isOpen={show} isBlocking={true}>
            <div style={{display:"grid",gridTemplateRows:"auto 2rem",justifyItems:"center",padding:24}}>
                
                <TextField label="New Update" multiline rows={10} onChange={(e) => seteditedText(e.target.value)} onGetErrorMessage={getErrorMessage}/>
                <div style={{padding:4}}>
                    <PrimaryButton styles={{root:{margin:4}}} text="Submit" onClick={handleSubmit}/>
                    <DefaultButton styles={{root:{margin:4}}} onClick={() => setshow(false)} text="Cancel"/>
                </div>
            </div>
        </Modal>
    </>;
}